<template>
  <view>
    <!-- 横向滑动的swiper容器 -->
    <uni-swiper :current="currentTab" @change="swiperChange" style="height: 100%;">
      <!-- 每个tab对应的swiper-item -->
      <uni-swiper-item v-for="(tab, index) in tabs" :key="index">
        <!-- 内容区域，这里可以放置具体的页面内容 -->
        <view class="content">
          <text>{{ tab }}</text>
        </view>
      </uni-swiper-item>
    </uni-swiper>
  </view>
</template>

<script>
export default {
  data() {
    return {
      // 当前选中的tab索引
      currentTab: 0,
      // tab的内容，可以根据需要自行添加或修改
      tabs: ['Tab 1', 'Tab 2', 'Tab 3'],
    };
  },
  methods: {
    // swiper切换时触发
    swiperChange(e) {
      this.currentTab = e.detail.current;
    },
  },
};
</script>

